import { Fragment, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { Container, Form, Button, Col, Row, Alert } from 'react-bootstrap';
import validator from 'validator'
import Axios from 'axios';
import './AddEmployee.css';

function AddEmployee(props) {
    const [upflag,setUpflag]=useState(false)
    const [employee, setEmployee] = useState({
        Name : "",
        Email: "",
        Age: "",
        Phone: "",
        Address : ""
    })
    const [emailFlag, setEmailFlag] = useState(false)
    const [emailError, setEmailError] = useState('')
    const validateEmail = (e) => {
    var email = e.target.value
        
    if (validator.isEmail(email)) {
        setEmailError('Valid Email')
        setEmailFlag(true)
    } else {
      setEmailError('Enter a valid Email')
    }
    }
    const handleChange = (event) => {
        setEmployee({
            ...employee,
            [event.target.name]: event.target.value
        })
    }

    const emailHandle = (event) => {
        setEmployee({
            ...employee,
            Email: event.target.value
        })
        validateEmail(event)
    }
    
    const history=useHistory()
    

    const addEmployeeHandler = () => {
        
        if (emailFlag == true) {
            setUpflag(true)
            Axios.post("https://employeedashboard110.herokuapp.com/addemp", {
                name: employee.Name,
                age: employee.Age,
                email: employee.Email,
                phone: employee.Phone,
                address: employee.Address
            }).then((response) => {
                console.log("Success",employee)
                
               
            }).catch((error)=>{
                console.log("Something wrong",error)
            })   
        
           
       }
            
        
    }
    const dashboardhandler = () => {
        history.push('/')
    }
    return (
        <Fragment>
            <Container className="form-container">
            <span className="title">ADD EMPLOYEE</span>
            <Button  variant="warning" size="sm" className="add-btn" onClick={dashboardhandler}>Dashboard</Button>
            </Container>
            <Container className="table-container">
            
                <Form className="add-form" to="/employee">
                    <Row>
                    <Col md>
                    <Form.Group className="mb-3">
                    <Form.Label>Employee Name</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Enter Name"
                                    name="Name"
                                    value={employee.Name}
                                    onChange={handleChange} />
                
                    </Form.Group>
                    </Col>         
                    <Col md>
                    <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Email</Form.Label>
                                <Form.Control
                                    type="email"
                                    placeholder="Enter email"
                                    name="Email"
                                    value={employee.Email}
                                    onChange={emailHandle}
                                />
                    {emailError}
                    </Form.Group>
                    </Col>
                                   
                    </Row>
                    <Row>
                    <Col md>
                    <Form.Group className="mb-3">
                    <Form.Label>Age</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Enter Age"
                                    name="Age"
                                    value={employee.Age}
                                    onChange={handleChange} />
                
                    </Form.Group>
                    </Col>         
                    <Col md>
                    <Form.Group className="mb-3">
                    <Form.Label>Phone</Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder="Enter Phone Number"
                                    name="Phone"
                                    value={employee.Phone}
                                    onChange={handleChange} />
                
                    </Form.Group>
                    </Col>
                                   
                    </Row>
                    <Form.Group className="mb-3" >
                    <Form.Label>Address</Form.Label>
                        <Form.Control
                            placeholder="Enter Address"
                            name="Address"
                            value={employee.Address}
                            onChange={handleChange} />
                    </Form.Group>
            
                    <Button variant="primary" type="submit" onClick={addEmployeeHandler}>
                    Add Employee
                    </Button>
                    {upflag && <Alert variant="success" className="up-alert">Updated Successfully</Alert>}
                </Form>
            
            </Container>
            
            
        </Fragment>
      
  );
}

export default AddEmployee;
