import { Fragment, useEffect, useState } from 'react';
import { Container, Form, Button, Col, Row } from 'react-bootstrap';
//import { BsFillLayersFill,BsFillPersonFill,BsCalendarFill,BsEnvelopeFill,BsBuilding,BsPhone} from 'react-icons/bs';

import './EmployeeDetails.css';
import Axios from 'axios';

function EditDetails(props) {
    const [empDetails,setEmpDetails]=useState([])
    const [isEmailValid,setIsEmailValid]=useState(false)
    
    
     useEffect(() => {
        const empid = props.employeeId
        Axios.get(`https://employeedashboard110.herokuapp.com/viewemployee/${empid}`)
        .then((response) => {
            console.log(response.data[0].Name)
            setEmpDetails(response.data[0])
            //setIsEmpLoad(true)
        }).catch((error) => {
            console.log("Something wrong")
        }) 
    }, [])

    const nameHandler = (event) => {
        setEmpDetails({...empDetails, Name: event.target.value})
    }
    const emailHandler = (event) => {
        setEmpDetails({...empDetails, Email: event.target.value})
    }
    const ageHandler = (event) => {
        setEmpDetails({...empDetails, Age: event.target.value})
    }
    const phoneHandler = (event) => {
        setEmpDetails({...empDetails, Phone: event.target.value})
    }
    const addressHandler = (event) => {
        setEmpDetails({...empDetails, Address: event.target.value})
    }
    


    const updateEmployeeHandler = (e) => {
        
        const empid = props.employeeId
        //console.log(name)
        Axios.put(`http://localhost:3001/updateemployee/${empid}`, {
            name: empDetails.Name,
            age: empDetails.Age,
            email: empDetails.Email,
            phone: empDetails.Phone,
            address: empDetails.Address
        }).then((response) => {
            console.log("Success")
            //props.viewHandler(2)
        }).catch((error)=>{
            console.log("Something wrong",error)
        })
    }
    const dashboardHandler = () => {
        props.viewHandler(1)
    }
    return (
        <Fragment>
            <Container className="form-container">
            <span className="title">UPDATE EMPLOYEE DETAILS</span>
            <Button onClick={dashboardHandler} variant="warning" size="sm" className="add-btn">Dashboard</Button>
            </Container>
            <Container className="table-container">
                <Form className="add-form">
                    <Row>
                    <Col md>
                    <Form.Group className="mb-3">
                    <Form.Label>Employee Name</Form.Label>
                                <Form.Control type="text" value={empDetails.Name} onChange={ nameHandler}/>
                
                    </Form.Group>
                    </Col>         
                    <Col md>
                    <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Email</Form.Label>
                    <Form.Control type="email" value={empDetails.Email} onChange={ emailHandler}/>
                
                    </Form.Group>
                    </Col>
                                   
                    </Row>
                    <Row>
                    <Col md>
                    <Form.Group className="mb-3">
                    <Form.Label>Age</Form.Label>
                            <Form.Control type="text" value={empDetails.Age}  onChange={ ageHandler}/>
                
                    </Form.Group>
                    </Col>         
                    <Col md>
                    <Form.Group className="mb-3">
                    <Form.Label>Phone</Form.Label>
                    <Form.Control type="text" value={empDetails.Phone}  onChange={ phoneHandler}/>
                
                    </Form.Group>
                    </Col>
                                   
                    </Row>
                    <Form.Group className="mb-3" >
                    <Form.Label>Address</Form.Label>
                    <Form.Control type="text" value={empDetails.Address}  onChange={ addressHandler}/>
                    </Form.Group>
            
                    <Button variant="primary" type="submit" onClick={updateEmployeeHandler}>
                    Update Employee
                    </Button>
                </Form>
            </Container>
        </Fragment>
      
  );
}

export default EditDetails;
