import { Fragment, useState } from 'react';

import './Employee.css';
import EmployeeDetails from './EmployeeDetails';
import Dashboard from './Dashboard';
import EditDetails from './EditDetails';


function Employee() {
    const [componentEnabler, setComponentEnabler] = useState(1)
    //const [isEmpLoad, setIsEmpLoad] = useState(false)
    const [selectedEmployeeId,setSelectedEmployeeId] = useState()
    

    
    switch (componentEnabler)
    {
        case 1 : return <Dashboard viewEnable={setComponentEnabler} employeeSelectedId={setSelectedEmployeeId}/>
    
    
        case 2 : return <EmployeeDetails viewHandler={setComponentEnabler} employeeId={selectedEmployeeId}/>
    
    
        case 3 : return <EditDetails viewHandler={setComponentEnabler} employeeId={selectedEmployeeId}/>
    }
                
        
        
        
        
 
}

export default Employee;
