import { Fragment, useState,useEffect } from 'react';
import { Container, Table, Button, Spinner, Modal } from 'react-bootstrap';
import { BsFillXCircleFill,BsPencil,BsPlay} from 'react-icons/bs';

import { useHistory } from 'react-router-dom';
import Axios from 'axios';
import './Dashboard.css';


function Dashboard(props) {
    const [dempid,setdEmpid] = useState()
    const [isEmpLoad, setIsEmpLoad] = useState(false)
    const [deleteflag,setDeleteflag] = useState(false)
    const [employeeList,setEmployeeList] = useState([])
    const history = useHistory()
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = (empid) => { setShow(true); setdEmpid(empid) };
    useEffect(() => {
        Axios.get("https://employeedashboard110.herokuapp.com/listemployee")
            .then((response) => {
                console.log(response)
                setEmployeeList(response.data)
                setIsEmpLoad(true)
            }).catch((error) => {
                console.log("Something wrong")
            })
    },[employeeList])
    const viewEmployeeDetails = (empid) => {
        props.viewEnable(2)
        //console.log("Employee Id",empid)
        props.employeeSelectedId(empid)
    }
    const editEmployee = (empid) => {
        props.viewEnable(3)
        //console.log("Employee Id",empid)
        props.employeeSelectedId(empid)
    }
    const deleteEmployee = () => {
        var empid = dempid;
        Axios.delete(`http://localhost:3001/deleteemployee/${empid}`).then((response) => {
            console.log("Success delete",response.data)
            
        }).catch((error)=>{
            console.log("Something wrong",error)
        })
        setShow(false)
    }
    const addEmployeeHandler = (path) => {
        history.push(path)
    }
    return (
        <Fragment>
       
                
                        <Container className="form-container">
                            <span className="title">DASHBOARD</span>
                            <Button onClick={() => addEmployeeHandler('employee')} variant="warning" size="sm" className="add-btn">Add New Employee</Button>
                        </Container>
                        <Container className="table-container">
                            <Table striped borderless className="employee-table">
                                <thead className="table-header">
                                <tr>
                                    <th>Emp ID</th>
                                    <th>Employee Name</th>
                                    <th>&nbsp;</th>
                                </tr>
                                </thead>
                                <tbody>
                                {isEmpLoad ?
                                (<>{employeeList.map((item) => {
                                    return (
                                        <tr key={item.Empid}>
                                            <td>{item.Empid}</td>
                                            <td>{item.Name}</td>
                                            <td>
                                            <Button onClick={()=>handleShow(item.Empid)} variant="danger" size="sm" className="view-btn"><BsFillXCircleFill className="icon"/> Delete</Button>
                                            <Button onClick={() => editEmployee(item.Empid)} variant="success" size="sm" className="view-btn"><BsPencil className="icon"/> Update </Button>
                                            <Button onClick={()=>viewEmployeeDetails(item.Empid)} variant="primary" size="sm" className="view-btn"><BsPlay className="icon"/> View</Button>

                                            </td>
                                            <Modal show={show} onHide={handleClose} animation={false}>
                                                <Modal.Header closeButton>
                                                <Modal.Title>Delete Employee</Modal.Title>
                                                </Modal.Header>
                                                <Modal.Body>Are you sure you want to delete</Modal.Body>
                                                <Modal.Footer>
                                                <Button variant="secondary" onClick={handleClose}>
                                                Cancel
                                                </Button>
                                                <Button variant="primary" onClick={deleteEmployee}>
                                                Delete
                                                </Button>
                                                </Modal.Footer>
                                            </Modal>                
                                        </tr>
                                        
                                    )
                                    
                                })}</>)
                                :(<tr>
                                    <td colSpan="3">Fetching data <br/><Spinner animation="border" role="status" size="sm">
                                    </Spinner></td>
                                             
                                 </tr>)
                                            
                                }
                                    
                                
                                
          
                                </tbody>
                </Table>
                
                        </Container>          
                
        
        </Fragment >
  );
}

export default Dashboard;
