import { Fragment, useEffect, useState } from 'react';
import { Container, Table, Button } from 'react-bootstrap';
import { BsFillLayersFill,BsFillPersonFill,BsCalendarFill,BsEnvelopeFill,BsBuilding,BsPhone} from 'react-icons/bs';

import './EmployeeDetails.css';
import Axios from 'axios';

function EmployeeDetails(props) {
    const [empDetails,setEmpDetails]=useState([])
    useEffect(() => {
        const empid = props.employeeId
        Axios.get(`https://employeedashboard110.herokuapp.com/viewemployee/${empid}`)
        .then((response) => {
            console.log(response.data[0].Name)
            setEmpDetails(response.data[0])
            //setIsEmpLoad(true)
        }).catch((error) => {
            console.log("Something wrong")
        }) 
    },[props.employeeId])
    const dashboardHandler = () => {
        props.viewHandler(1)
    }
    return (
        <Fragment>
            <Container className="form-container">
            <span className="title">EMPLOYEE DETAILS</span>
            <Button onClick={dashboardHandler} variant="warning" size="sm" className="add-btn">Dashboard</Button>
            </Container>
            <Container className="table-container">
            <Table striped borderless className="employee-details-table">
                    <tbody>
                        <tr>
                            <td><BsFillLayersFill className="icon"/> Emp ID :  {empDetails.Empid}</td>
                                
                        </tr>
                        <tr>
                            <td><BsFillPersonFill className="icon"/> Name :  {empDetails.Name}</td>
                                
                        </tr>
                        <tr>
                            <td><BsCalendarFill className="icon"/> Age : {empDetails.Age}</td>
                                
                        </tr>
                        <tr>
                            <td><BsEnvelopeFill className="icon"/> Email : {empDetails.Email}</td>
                                
                        </tr>
                        <tr>
                            <td><BsPhone className="icon"/> Phone : {empDetails.Phone}</td>
                                
                        </tr>
                        <tr>
                            <td><BsBuilding className="icon"/> Address : {empDetails.Address}</td>
                                
                        </tr>
                        
                                
          
                    </tbody>
            </Table>
            </Container>
        </Fragment>
      
  );
}

export default EmployeeDetails;
