import { Route, Switch } from 'react-router-dom'
import './App.css';
import Employee from './components/Employee'
import AddEmployee from './components/AddEmployee'


function App() {
  return (
    <div className="App">

       
        <Switch>
          <Route path="/" exact>

            <Employee/>

          </Route>
          <Route path="/employee">
            
            <AddEmployee/>

          </Route>
        </Switch>
        
    

      
    </div>
  );
}

export default App;
